@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  /* overflow: hidden; */
  background: #272727;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
}

.absolute {
  position: absolute;
}
.centered {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.cursor {
  color: white;
}
* {
    box-sizing: border-box;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    /* background: #2d4967; */
    font-family: inter;
    color: #fff;
    text-shadow:
        0 0 3px #fff,
        0 0 7px #fff,
        0 0 15px #fff,
        0 0 30px #0fa,
        0 0 50px #0fa,
        0 0 70px #0fa,
        0 0 90px #0fa,
        0 0 140px #0fa;

}

canvas {
    touch-action: none;
}

.annotation {
    cursor: pointer;
    outline: none;
    border: none;
    font-size: 14px;
    font-weight: 400;
    /* background: black; */
    color: #fff;
    padding: 2px 10px;
    border-radius: 20px;
    letter-spacing: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
}
.headTitle {
        text-align: center; 
}
.the-neon-sign {
	border: 5px #fff solid;
	border-radius: 7px;
	box-shadow: 0 0 10px #ff18a7, 0 0 10px #ff18a7 inset, 0 0 30px #ff18a7,
		0 0 30px #ff18a7 inset;
	margin-top: 3rem;
	/* position: relative; */
